import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import {webRoute} from './router/web'
import { AppComponent } from './app.component'
import { CarouselComponent } from './bulu/middle.troops/carousel/carousel.component'

const routes: Routes = [...webRoute,
    {path:'body/carousel',component:CarouselComponent},
    {path:'homepage',component:AppComponent}
  ];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
